const galleryList = document.querySelector('.js-gallery');
const lightbox = document.querySelector('.js-lightbox');
const lightboxOverlay = document.querySelector('.lightbox__overlay');
const lightboxButton = document.querySelector('.lightbox__button');
const lightboxButtonClose = document.querySelector('button[data-action="close-lightbox"]');
const lightboxContent = document.querySelector('.lightbox__content');
const lightboxImage = document.querySelector('.lightbox__image');

const galleryListItems = galleryItems
  .map(
    ({ preview, original, description }, index) => `<li  class="gallery__item">
  <a
    class="gallery__link"
    href= "${original}"
   
  >
    <img
      class="gallery__image"
      src= "${preview}"
      data-source= "${original}"
      alt="${description}"
       data-index=${index}
    />
  </a>
</li>`,
  )
  .join('');

galleryList.insertAdjacentHTML('afterbegin', galleryListItems);

function onClickHandler(event) {
  event.preventDefault();

  if (event.target.classList.contains('gallery__image')) {
    lightbox.classList.add('is-open');

    const source = event.target.dataset.source;
    const alt = event.target.getAttribute('alt');
    lightboxImage.setAttribute('src', source);
    lightboxImage.setAttribute('alt', alt);
  }
}

function onCloseHandler(event) {
  if (event.target.dataset.action) {
    lightbox.classList.remove('is-open');
    lightboxImage.setAttribute('src', '');
    lightboxImage.setAttribute('alt', '');
  }
}

function modalClose(event) {
  if (event.keyCode === 27) {
    lightbox.classList.remove('is-open');
    lightbox.classList.remove('is-open');
    lightboxImage.setAttribute('src', '');
    lightboxImage.setAttribute('alt', '');
  }
}

function closeOnOverlay(event) {
  if (event.target !== lightboxContent) {
    lightbox.classList.remove('is-open');
    lightbox.classList.remove('is-open');
    lightboxImage.setAttribute('src', '');
    lightboxImage.setAttribute('alt', '');
  }
}

galleryList.addEventListener('click', onClickHandler);
lightboxButtonClose.addEventListener('click', onCloseHandler);
document.addEventListener('keydown', modalClose);
lightboxOverlay.addEventListener('click', closeOnOverlay);
